import React, { useState, useEffect, useCallback } from 'react';
import { Link } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import { PlusIcon, BookOpenIcon, EyeIcon, HeartIcon, ShareIcon } from '@heroicons/react/24/outline';
import { PencilIcon, TrashIcon, GlobeAltIcon, LockClosedIcon } from '@heroicons/react/24/solid';
import toast from 'react-hot-toast';

const Dashboard = () => {
  const { user, token } = useAuth();
  const [poems, setPoems] = useState([]);
  const [loading, setLoading] = useState(true);
  const [stats, setStats] = useState({
    totalPoems: 0,
    totalViews: 0,
    totalLikes: 0,
    totalShares: 0
  });

  const fetchUserPoems = useCallback(async () => {
    try {
      const API_BASE_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000/api';
      const response = await fetch(`${API_BASE_URL}/users/${user.id}/poems`, {
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });

      if (!response.ok) {
        throw new Error('Failed to fetch poems');
      }

      const data = await response.json();
      setPoems(data.poems || []);
      
      // Calculate stats
      const totalViews = data.poems?.reduce((sum, poem) => sum + (poem.views || 0), 0) || 0;
      const totalLikes = data.poems?.reduce((sum, poem) => sum + (poem.likes?.length || 0), 0) || 0;
      const totalShares = data.poems?.reduce((sum, poem) => sum + (poem.shares || 0), 0) || 0;
      
      setStats({
        totalPoems: data.poems?.length || 0,
        totalViews,
        totalLikes,
        totalShares
      });
    } catch (error) {
      console.error('Error fetching poems:', error);
      toast.error('Failed to load your poems');
    } finally {
      setLoading(false);
    }
  }, [user, token]);

  useEffect(() => {
    fetchUserPoems();
  }, [fetchUserPoems]);

  const handleDeletePoem = async (poemId) => {
    if (!window.confirm('Are you sure you want to delete this poem? This action cannot be undone.')) {
      return;
    }

    try {
      const API_BASE_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000/api';
      const response = await fetch(`${API_BASE_URL}/poems/${poemId}`, {
        method: 'DELETE',
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });

      if (!response.ok) {
        throw new Error('Failed to delete poem');
      }

      setPoems(poems.filter(poem => poem.id !== poemId && poem._id !== poemId));
      toast.success('Poem deleted successfully');
      
      // Recalculate stats
      const updatedPoems = poems.filter(poem => poem.id !== poemId && poem._id !== poemId);
      const totalViews = updatedPoems.reduce((sum, poem) => sum + (poem.views || 0), 0);
      const totalLikes = updatedPoems.reduce((sum, poem) => sum + (poem.likes?.length || 0), 0);
      const totalShares = updatedPoems.reduce((sum, poem) => sum + (poem.shares || 0), 0);
      
      setStats({
        totalPoems: updatedPoems.length,
        totalViews,
        totalLikes,
        totalShares
      });
    } catch (error) {
      console.error('Error deleting poem:', error);
      toast.error('Failed to delete poem');
    }
  };

  const formatDate = (dateString) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric'
    });
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-slate-50 to-blue-50 py-8">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="animate-pulse">
            <div className="h-8 bg-gray-200 rounded w-1/4 mb-6"></div>
            <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-8">
              {[...Array(4)].map((_, i) => (
                <div key={i} className="bg-white p-6 rounded-lg shadow-sm">
                  <div className="h-4 bg-gray-200 rounded w-1/2 mb-2"></div>
                  <div className="h-8 bg-gray-200 rounded w-3/4"></div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-blue-50 py-8">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Header */}
        <div className="flex justify-between items-center mb-8">
          <div>
            <h1 className="text-3xl font-bold text-gray-900">
              Welcome back, {user?.displayName || user?.username}!
            </h1>
            <p className="text-gray-600 mt-1">Manage your poetry and track your impact</p>
          </div>
          <Link
            to="/write"
            className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
          >
            <PlusIcon className="h-4 w-4 mr-2" />
            Write New Poem
          </Link>
        </div>

        {/* Stats Grid */}
        <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-8">
          <div className="bg-white overflow-hidden shadow-sm rounded-lg">
            <div className="p-6">
              <div className="flex items-center">
                <div className="flex-shrink-0">
                  <BookOpenIcon className="h-6 w-6 text-indigo-600" />
                </div>
                <div className="ml-5 w-0 flex-1">
                  <dl>
                    <dt className="text-sm font-medium text-gray-500 truncate">Total Poems</dt>
                    <dd className="text-lg font-medium text-gray-900">{stats.totalPoems}</dd>
                  </dl>
                </div>
              </div>
            </div>
          </div>

          <div className="bg-white overflow-hidden shadow-sm rounded-lg">
            <div className="p-6">
              <div className="flex items-center">
                <div className="flex-shrink-0">
                  <EyeIcon className="h-6 w-6 text-green-600" />
                </div>
                <div className="ml-5 w-0 flex-1">
                  <dl>
                    <dt className="text-sm font-medium text-gray-500 truncate">Total Views</dt>
                    <dd className="text-lg font-medium text-gray-900">{stats.totalViews}</dd>
                  </dl>
                </div>
              </div>
            </div>
          </div>

          <div className="bg-white overflow-hidden shadow-sm rounded-lg">
            <div className="p-6">
              <div className="flex items-center">
                <div className="flex-shrink-0">
                  <HeartIcon className="h-6 w-6 text-red-600" />
                </div>
                <div className="ml-5 w-0 flex-1">
                  <dl>
                    <dt className="text-sm font-medium text-gray-500 truncate">Total Likes</dt>
                    <dd className="text-lg font-medium text-gray-900">{stats.totalLikes}</dd>
                  </dl>
                </div>
              </div>
            </div>
          </div>

          <div className="bg-white overflow-hidden shadow-sm rounded-lg">
            <div className="p-6">
              <div className="flex items-center">
                <div className="flex-shrink-0">
                  <ShareIcon className="h-6 w-6 text-blue-600" />
                </div>
                <div className="ml-5 w-0 flex-1">
                  <dl>
                    <dt className="text-sm font-medium text-gray-500 truncate">Total Shares</dt>
                    <dd className="text-lg font-medium text-gray-900">{stats.totalShares}</dd>
                  </dl>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Poems Section */}
        <div className="bg-white shadow-sm rounded-lg">
          <div className="px-6 py-4 border-b border-gray-200">
            <h2 className="text-lg font-medium text-gray-900">Your Poems</h2>
            <p className="text-sm text-gray-500">All your published works with copyright protection</p>
          </div>

          {poems.length === 0 ? (
            <div className="text-center py-12">
              <BookOpenIcon className="mx-auto h-12 w-12 text-gray-400" />
              <h3 className="mt-2 text-sm font-medium text-gray-900">No poems yet</h3>
              <p className="mt-1 text-sm text-gray-500">Get started by writing your first poem.</p>
              <div className="mt-6">
                <Link
                  to="/write"
                  className="inline-flex items-center px-4 py-2 border border-transparent shadow-sm text-sm font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                >
                  <PlusIcon className="h-4 w-4 mr-2" />
                  Write Your First Poem
                </Link>
              </div>
            </div>
          ) : (
            <div className="divide-y divide-gray-200">
              {poems.map((poem) => (
                <div key={poem.id || poem._id} className="p-6 hover:bg-gray-50">
                  <div className="flex items-center justify-between">
                    <div className="flex-1 min-w-0">
                      <div className="flex items-center space-x-3">
                        <Link
                          to={`/poems/${poem.id || poem._id}`}
                          className="text-lg font-medium text-gray-900 hover:text-indigo-600 truncate"
                        >
                          {poem.title}
                        </Link>
                        <div className="flex items-center space-x-1">
                          {poem.visibility === 'public' ? (
                            <GlobeAltIcon className="h-4 w-4 text-green-500" title="Public" />
                          ) : (
                            <LockClosedIcon className="h-4 w-4 text-gray-500" title="Private" />
                          )}
                          <span className="inline-flex items-center px-2 py-0.5 rounded text-xs font-medium bg-indigo-100 text-indigo-800">
                            {poem.license}
                          </span>
                        </div>
                      </div>
                      <div className="mt-1 flex items-center space-x-4 text-sm text-gray-500">
                        <span>Created {formatDate(poem.createdAt)}</span>
                        <span>{poem.views || 0} views</span>
                        <span>{poem.likes?.length || 0} likes</span>
                        {poem.tags && poem.tags.length > 0 && (
                          <span className="truncate">
                            Tags: {poem.tags.slice(0, 3).join(', ')}
                            {poem.tags.length > 3 && '...'}
                          </span>
                        )}
                      </div>
                    </div>
                    <div className="flex items-center space-x-2">
                      <Link
                        to={`/write?edit=${poem.id || poem._id}`}
                        className="inline-flex items-center p-2 border border-gray-300 rounded-md text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                        title="Edit poem"
                      >
                        <PencilIcon className="h-4 w-4" />
                      </Link>
                      <button
                        onClick={() => handleDeletePoem(poem.id || poem._id)}
                        className="inline-flex items-center p-2 border border-red-300 rounded-md text-red-700 bg-white hover:bg-red-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-red-500"
                        title="Delete poem"
                      >
                        <TrashIcon className="h-4 w-4" />
                      </button>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default Dashboard;