import React, { useState, useEffect } from 'react';
import { useSearchParams, Link } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import { 
  MagnifyingGlassIcon, 
  AdjustmentsHorizontalIcon,
  BookOpenIcon,
  UserIcon,
  EyeIcon,
  HeartIcon
} from '@heroicons/react/24/outline';
import toast from 'react-hot-toast';

const SearchPage = () => {
  const [searchParams, setSearchParams] = useSearchParams();
  const { token } = useAuth();
  
  const [query, setQuery] = useState(searchParams.get('q') || '');
  const [filters, setFilters] = useState({
    type: searchParams.get('type') || 'all', // all, poems, authors
    tag: searchParams.get('tag') || '',
    mood: searchParams.get('mood') || '',
    style: searchParams.get('style') || '',
    license: searchParams.get('license') || '',
    sortBy: searchParams.get('sort') || 'relevance' // relevance, date, likes, views
  });
  
  const [results, setResults] = useState([]);
  const [loading, setLoading] = useState(false);
  const [totalResults, setTotalResults] = useState(0);
  const [hasSearched, setHasSearched] = useState(false);

  useEffect(() => {
    const q = searchParams.get('q');
    const tag = searchParams.get('tag');
    
    if (q || tag) {
      if (tag) {
        setFilters(prev => ({ ...prev, tag }));
      }
      handleSearch();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [searchParams]);

  const handleSearch = async (e) => {
    if (e) e.preventDefault();
    
    if (!query.trim() && !filters.tag) {
      toast.error('Please enter a search term or select filters');
      return;
    }

    setLoading(true);
    setHasSearched(true);

    try {
      const API_BASE_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000/api';
      const params = new URLSearchParams();
      
      if (query) params.append('q', query);
      if (filters.type !== 'all') params.append('type', filters.type);
      if (filters.tag) params.append('tag', filters.tag);
      if (filters.mood) params.append('mood', filters.mood);
      if (filters.style) params.append('style', filters.style);
      if (filters.license) params.append('license', filters.license);
      if (filters.sortBy !== 'relevance') params.append('sort', filters.sortBy);

      const response = await fetch(`${API_BASE_URL}/search?${params}`, {
        headers: token ? { 'Authorization': `Bearer ${token}` } : {}
      });

      if (!response.ok) {
        throw new Error('Search failed');
      }

      const data = await response.json();
      setResults(data.results || []);
      setTotalResults(data.total || 0);

      // Update URL with search params
      const newParams = new URLSearchParams();
      if (query) newParams.set('q', query);
      if (filters.type !== 'all') newParams.set('type', filters.type);
      if (filters.tag) newParams.set('tag', filters.tag);
      if (filters.mood) newParams.set('mood', filters.mood);
      if (filters.style) newParams.set('style', filters.style);
      if (filters.license) newParams.set('license', filters.license);
      if (filters.sortBy !== 'relevance') newParams.set('sort', filters.sortBy);
      
      setSearchParams(newParams);

    } catch (error) {
      console.error('Search error:', error);
      toast.error('Search failed. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  const handleFilterChange = (key, value) => {
    setFilters(prev => ({ ...prev, [key]: value }));
  };

  const clearFilters = () => {
    setFilters({
      type: 'all',
      tag: '',
      mood: '',
      style: '',
      license: '',
      sortBy: 'relevance'
    });
    setQuery('');
    setResults([]);
    setHasSearched(false);
    setSearchParams({});
  };

  const formatDate = (dateString) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric'
    });
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-blue-50 py-8">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Search Header */}
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-gray-900 mb-4">Discover Poetry</h1>
          <p className="text-gray-600">Search through thousands of poems and discover amazing poets</p>
        </div>

        {/* Search Form */}
        <form onSubmit={handleSearch} className="mb-8">
          <div className="flex gap-4">
            <div className="flex-1 relative">
              <MagnifyingGlassIcon className="absolute left-3 top-1/2 transform -translate-y-1/2 h-5 w-5 text-gray-400" />
              <input
                type="text"
                value={query}
                onChange={(e) => setQuery(e.target.value)}
                placeholder="Search poems, authors, or keywords..."
                className="block w-full pl-10 pr-3 py-3 border border-gray-300 rounded-md leading-5 bg-white placeholder-gray-500 focus:outline-none focus:placeholder-gray-400 focus:ring-1 focus:ring-indigo-500 focus:border-indigo-500"
              />
            </div>
            <button
              type="submit"
              disabled={loading}
              className="px-6 py-3 border border-transparent text-base font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 disabled:opacity-50"
            >
              {loading ? 'Searching...' : 'Search'}
            </button>
          </div>
        </form>

        <div className="grid grid-cols-1 lg:grid-cols-4 gap-8">
          {/* Filters Sidebar */}
          <div className="lg:col-span-1">
            <div className="bg-white rounded-lg shadow-sm p-6">
              <div className="flex items-center justify-between mb-4">
                <h3 className="text-lg font-medium text-gray-900 flex items-center">
                  <AdjustmentsHorizontalIcon className="h-5 w-5 mr-2" />
                  Filters
                </h3>
                <button
                  onClick={clearFilters}
                  className="text-sm text-indigo-600 hover:text-indigo-500"
                >
                  Clear all
                </button>
              </div>

              <div className="space-y-6">
                {/* Content Type */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Content Type
                  </label>
                  <select
                    value={filters.type}
                    onChange={(e) => handleFilterChange('type', e.target.value)}
                    className="block w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                  >
                    <option value="all">All</option>
                    <option value="poems">Poems</option>
                    <option value="authors">Authors</option>
                  </select>
                </div>

                {/* Tag Filter */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Tag
                  </label>
                  <input
                    type="text"
                    value={filters.tag}
                    onChange={(e) => handleFilterChange('tag', e.target.value)}
                    placeholder="Enter tag..."
                    className="block w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                  />
                </div>

                {/* Mood Filter */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Mood
                  </label>
                  <select
                    value={filters.mood}
                    onChange={(e) => handleFilterChange('mood', e.target.value)}
                    className="block w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                  >
                    <option value="">Any</option>
                    <option value="joyful">Joyful</option>
                    <option value="melancholic">Melancholic</option>
                    <option value="contemplative">Contemplative</option>
                    <option value="passionate">Passionate</option>
                    <option value="serene">Serene</option>
                    <option value="dark">Dark</option>
                    <option value="hopeful">Hopeful</option>
                  </select>
                </div>

                {/* Style Filter */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Style
                  </label>
                  <select
                    value={filters.style}
                    onChange={(e) => handleFilterChange('style', e.target.value)}
                    className="block w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                  >
                    <option value="">Any</option>
                    <option value="free-verse">Free Verse</option>
                    <option value="sonnet">Sonnet</option>
                    <option value="haiku">Haiku</option>
                    <option value="ballad">Ballad</option>
                    <option value="limerick">Limerick</option>
                    <option value="narrative">Narrative</option>
                    <option value="lyric">Lyric</option>
                  </select>
                </div>

                {/* Sort By */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Sort By
                  </label>
                  <select
                    value={filters.sortBy}
                    onChange={(e) => handleFilterChange('sortBy', e.target.value)}
                    className="block w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                  >
                    <option value="relevance">Relevance</option>
                    <option value="date">Newest First</option>
                    <option value="likes">Most Liked</option>
                    <option value="views">Most Viewed</option>
                  </select>
                </div>

                <button
                  onClick={handleSearch}
                  className="w-full px-4 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                >
                  Apply Filters
                </button>
              </div>
            </div>
          </div>

          {/* Results */}
          <div className="lg:col-span-3">
            {loading ? (
              <div className="bg-white rounded-lg shadow-sm p-8">
                <div className="animate-pulse space-y-4">
                  {[...Array(5)].map((_, i) => (
                    <div key={i} className="border-b border-gray-200 pb-4 last:border-b-0">
                      <div className="h-4 bg-gray-200 rounded w-3/4 mb-2"></div>
                      <div className="h-3 bg-gray-200 rounded w-1/2 mb-2"></div>
                      <div className="h-3 bg-gray-200 rounded w-full"></div>
                    </div>
                  ))}
                </div>
              </div>
            ) : hasSearched ? (
              <div className="bg-white rounded-lg shadow-sm">
                <div className="px-6 py-4 border-b border-gray-200">
                  <h2 className="text-lg font-medium text-gray-900">
                    {totalResults} {totalResults === 1 ? 'Result' : 'Results'}
                    {query && ` for "${query}"`}
                  </h2>
                </div>

                {results.length === 0 ? (
                  <div className="text-center py-12">
                    <MagnifyingGlassIcon className="mx-auto h-12 w-12 text-gray-400" />
                    <h3 className="mt-2 text-sm font-medium text-gray-900">No results found</h3>
                    <p className="mt-1 text-sm text-gray-500">
                      Try adjusting your search terms or filters.
                    </p>
                  </div>
                ) : (
                  <div className="divide-y divide-gray-200">
                    {results.map((result) => (
                      <div key={`${result.type}-${result.id || result._id}`} className="p-6 hover:bg-gray-50">
                        {result.type === 'poem' ? (
                          <div>
                            <div className="flex items-start justify-between">
                              <div className="flex-1 min-w-0">
                                <div className="flex items-center space-x-2 mb-2">
                                  <BookOpenIcon className="h-4 w-4 text-indigo-600" />
                                  <span className="text-xs text-indigo-600 font-medium">POEM</span>
                                </div>
                                
                                <Link
                                  to={`/poems/${result.id || result._id}`}
                                  className="text-lg font-medium text-gray-900 hover:text-indigo-600 block mb-2"
                                >
                                  {result.title}
                                </Link>
                                
                                <div className="text-sm text-gray-600 mb-3 line-clamp-2">
                                  {result.body?.substring(0, 200)}...
                                </div>
                                
                                <div className="flex items-center space-x-4 text-xs text-gray-500">
                                  <Link 
                                    to={`/authors/${result.author?.id || result.author?._id}`}
                                    className="hover:text-indigo-600"
                                  >
                                    by {result.author?.displayName || result.author?.username}
                                  </Link>
                                  <span>{formatDate(result.createdAt)}</span>
                                  <div className="flex items-center">
                                    <EyeIcon className="h-3 w-3 mr-1" />
                                    {result.views || 0}
                                  </div>
                                  <div className="flex items-center">
                                    <HeartIcon className="h-3 w-3 mr-1" />
                                    {result.likes?.length || 0}
                                  </div>
                                </div>
                                
                                {result.tags && result.tags.length > 0 && (
                                  <div className="mt-2 flex flex-wrap gap-1">
                                    {result.tags.slice(0, 3).map((tag, index) => (
                                      <span
                                        key={index}
                                        className="inline-flex items-center px-2 py-0.5 rounded text-xs font-medium bg-indigo-100 text-indigo-800"
                                      >
                                        {tag}
                                      </span>
                                    ))}
                                  </div>
                                )}
                              </div>
                            </div>
                          </div>
                        ) : (
                          <div>
                            <div className="flex items-start justify-between">
                              <div className="flex-1 min-w-0">
                                <div className="flex items-center space-x-2 mb-2">
                                  <UserIcon className="h-4 w-4 text-green-600" />
                                  <span className="text-xs text-green-600 font-medium">AUTHOR</span>
                                </div>
                                
                                <Link
                                  to={`/authors/${result.id || result._id}`}
                                  className="text-lg font-medium text-gray-900 hover:text-indigo-600 block mb-2"
                                >
                                  {result.displayName || result.username}
                                </Link>
                                
                                {result.profile?.bio && (
                                  <div className="text-sm text-gray-600 mb-3">
                                    {result.profile.bio.substring(0, 200)}...
                                  </div>
                                )}
                                
                                <div className="flex items-center space-x-4 text-xs text-gray-500">
                                  <span>{result.poemsCount || 0} poems</span>
                                  <span>{result.followers || 0} followers</span>
                                  <span>Joined {formatDate(result.profile?.joinedAt || result.createdAt)}</span>
                                </div>
                              </div>
                            </div>
                          </div>
                        )}
                      </div>
                    ))}
                  </div>
                )}
              </div>
            ) : (
              <div className="bg-white rounded-lg shadow-sm p-12 text-center">
                <MagnifyingGlassIcon className="mx-auto h-12 w-12 text-gray-400" />
                <h3 className="mt-2 text-lg font-medium text-gray-900">Start Your Search</h3>
                <p className="mt-1 text-sm text-gray-500">
                  Enter keywords to discover amazing poetry and talented poets.
                </p>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default SearchPage;