import React, { useState, useEffect } from 'react';
import { useParams, Link, useNavigate } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import { 
  HeartIcon, 
  ShareIcon, 
  EyeIcon, 
  CalendarIcon,
  TagIcon,
  ShieldCheckIcon,
  UserIcon
} from '@heroicons/react/24/outline';
import { HeartIcon as HeartIconSolid, PencilIcon, TrashIcon } from '@heroicons/react/24/solid';
import toast from 'react-hot-toast';

const PoemPage = () => {
  const { poemId } = useParams();
  const navigate = useNavigate();
  const { user, token } = useAuth();
  const [poem, setPoem] = useState(null);
  const [loading, setLoading] = useState(true);
  const [liked, setLiked] = useState(false);
  const [likesCount, setLikesCount] = useState(0);

  useEffect(() => {
    if (poemId) {
      fetchPoem();
    }
  }, [poemId, token, user]);

  const fetchPoem = async () => {
    try {
      const API_BASE_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000/api';
      const response = await fetch(`${API_BASE_URL}/poems/${poemId}`, {
        headers: token ? { 'Authorization': `Bearer ${token}` } : {}
      });

      if (!response.ok) {
        throw new Error('Poem not found');
      }

      const data = await response.json();
      setPoem(data.poem);
      setLikesCount(data.poem.likes?.length || 0);
      
      if (user && data.poem.likes) {
        setLiked(data.poem.likes.includes(user.id));
      }
    } catch (error) {
      console.error('Error fetching poem:', error);
      toast.error('Failed to load poem');
      navigate('/explore');
    } finally {
      setLoading(false);
    }
  };

  const handleLike = async () => {
    if (!token) {
      toast.error('Please log in to like poems');
      return;
    }

    try {
      const API_BASE_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000/api';
      const response = await fetch(`${API_BASE_URL}/poems/${poemId}/like`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });

      if (!response.ok) {
        throw new Error('Failed to like poem');
      }

      setLiked(!liked);
      setLikesCount(liked ? likesCount - 1 : likesCount + 1);
    } catch (error) {
      console.error('Error liking poem:', error);
      toast.error('Failed to like poem');
    }
  };

  const handleShare = async () => {
    const url = window.location.href;
    const title = `"${poem.title}" by ${poem.author.displayName || poem.author.username}`;
    
    if (navigator.share) {
      try {
        await navigator.share({
          title,
          text: `Check out this beautiful poem on Poet's Codex`,
          url
        });
      } catch (error) {
        // User cancelled sharing
      }
    } else {
      // Fallback to clipboard
      try {
        await navigator.clipboard.writeText(`${title}\n\n${url}`);
        toast.success('Link copied to clipboard!');
      } catch (error) {
        toast.error('Failed to copy link');
      }
    }
  };

  const handleDelete = async () => {
    if (!window.confirm('Are you sure you want to delete this poem? This action cannot be undone.')) {
      return;
    }

    try {
      const API_BASE_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000/api';
      const response = await fetch(`${API_BASE_URL}/poems/${poemId}`, {
        method: 'DELETE',
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });

      if (!response.ok) {
        throw new Error('Failed to delete poem');
      }

      toast.success('Poem deleted successfully');
      navigate('/dashboard');
    } catch (error) {
      console.error('Error deleting poem:', error);
      toast.error('Failed to delete poem');
    }
  };

  const formatDate = (dateString) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    });
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-slate-50 to-blue-50 py-8">
        <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="animate-pulse">
            <div className="bg-white rounded-lg shadow-sm p-8">
              <div className="h-8 bg-gray-200 rounded w-3/4 mb-4"></div>
              <div className="h-4 bg-gray-200 rounded w-1/2 mb-6"></div>
              <div className="space-y-3">
                <div className="h-4 bg-gray-200 rounded"></div>
                <div className="h-4 bg-gray-200 rounded w-5/6"></div>
                <div className="h-4 bg-gray-200 rounded w-4/6"></div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }

  if (!poem) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-slate-50 to-blue-50 py-8">
        <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center py-12">
            <h3 className="mt-2 text-lg font-medium text-gray-900">Poem Not Found</h3>
            <p className="mt-1 text-sm text-gray-500">This poem doesn't exist or has been removed.</p>
            <div className="mt-6">
              <Link
                to="/explore"
                className="inline-flex items-center px-4 py-2 border border-transparent shadow-sm text-sm font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700"
              >
                Explore Other Poems
              </Link>
            </div>
          </div>
        </div>
      </div>
    );
  }

  const isOwner = user && poem.author && (user.id === poem.author.id || user.id === poem.author._id);

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-blue-50 py-8">
      <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="bg-white rounded-lg shadow-sm overflow-hidden">
          {/* Header */}
          <div className="px-8 py-6 border-b border-gray-200">
            <div className="flex items-start justify-between">
              <div className="flex-1">
                <h1 className="text-3xl font-bold text-gray-900 mb-4">{poem.title}</h1>
                
                {/* Author info */}
                <div className="flex items-center space-x-4 mb-4">
                  <Link 
                    to={`/authors/${poem.author.id || poem.author._id}`}
                    className="flex items-center space-x-2 text-sm text-gray-600 hover:text-indigo-600"
                  >
                    {poem.author.avatar ? (
                      <img 
                        src={poem.author.avatar} 
                        alt={poem.author.displayName || poem.author.username}
                        className="h-8 w-8 rounded-full"
                      />
                    ) : (
                      <div className="h-8 w-8 rounded-full bg-indigo-100 flex items-center justify-center">
                        <UserIcon className="h-5 w-5 text-indigo-600" />
                      </div>
                    )}
                    <span className="font-medium">
                      {poem.author.displayName || poem.author.username}
                    </span>
                  </Link>
                  
                  <div className="flex items-center text-xs text-gray-500">
                    <CalendarIcon className="h-4 w-4 mr-1" />
                    {formatDate(poem.createdAt)}
                  </div>
                </div>

                {/* Metadata */}
                <div className="flex items-center space-x-4 text-sm text-gray-500">
                  <div className="flex items-center">
                    <EyeIcon className="h-4 w-4 mr-1" />
                    {poem.views || 0} views
                  </div>
                  <div className="flex items-center">
                    <HeartIcon className="h-4 w-4 mr-1" />
                    {likesCount} likes
                  </div>
                  <div className="flex items-center">
                    <ShieldCheckIcon className="h-4 w-4 mr-1" />
                    {poem.license || 'All Rights Reserved'}
                  </div>
                </div>
              </div>

              {/* Action buttons */}
              <div className="flex items-center space-x-2">
                <button
                  onClick={handleLike}
                  className={`inline-flex items-center px-3 py-2 border rounded-md text-sm font-medium focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 ${
                    liked
                      ? 'border-red-300 text-red-700 bg-red-50 hover:bg-red-100'
                      : 'border-gray-300 text-gray-700 bg-white hover:bg-gray-50'
                  }`}
                >
                  {liked ? (
                    <HeartIconSolid className="h-4 w-4 mr-2 text-red-500" />
                  ) : (
                    <HeartIcon className="h-4 w-4 mr-2" />
                  )}
                  {likesCount}
                </button>
                
                <button
                  onClick={handleShare}
                  className="inline-flex items-center px-3 py-2 border border-gray-300 rounded-md text-sm font-medium text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                >
                  <ShareIcon className="h-4 w-4 mr-2" />
                  Share
                </button>

                {isOwner && (
                  <>
                    <Link
                      to={`/write?edit=${poem.id || poem._id}`}
                      className="inline-flex items-center px-3 py-2 border border-gray-300 rounded-md text-sm font-medium text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                    >
                      <PencilIcon className="h-4 w-4 mr-2" />
                      Edit
                    </Link>
                    
                    <button
                      onClick={handleDelete}
                      className="inline-flex items-center px-3 py-2 border border-red-300 rounded-md text-sm font-medium text-red-700 bg-white hover:bg-red-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-red-500"
                    >
                      <TrashIcon className="h-4 w-4 mr-2" />
                      Delete
                    </button>
                  </>
                )}
              </div>
            </div>
          </div>

          {/* Poem Content */}
          <div className="px-8 py-8">
            <div className="prose prose-lg max-w-none">
              <div className="whitespace-pre-line text-gray-800 font-serif leading-relaxed">
                {poem.body}
              </div>
            </div>
          </div>

          {/* Tags and additional info */}
          {(poem.tags && poem.tags.length > 0) || poem.mood || poem.style ? (
            <div className="px-8 py-4 bg-gray-50 border-t border-gray-200">
              <div className="flex items-center flex-wrap gap-4">
                {poem.tags && poem.tags.length > 0 && (
                  <div className="flex items-center space-x-2">
                    <TagIcon className="h-4 w-4 text-gray-500" />
                    <div className="flex flex-wrap gap-1">
                      {poem.tags.map((tag, index) => (
                        <Link
                          key={index}
                          to={`/explore?tag=${encodeURIComponent(tag)}`}
                          className="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium bg-indigo-100 text-indigo-800 hover:bg-indigo-200"
                        >
                          {tag}
                        </Link>
                      ))}
                    </div>
                  </div>
                )}
                
                {poem.mood && (
                  <span className="text-xs text-gray-500">
                    Mood: <span className="font-medium">{poem.mood}</span>
                  </span>
                )}
                
                {poem.style && (
                  <span className="text-xs text-gray-500">
                    Style: <span className="font-medium">{poem.style}</span>
                  </span>
                )}
              </div>
            </div>
          ) : null}
        </div>

        {/* Copyright Protection Notice */}
        <div className="mt-6 bg-indigo-50 border border-indigo-200 rounded-lg p-4">
          <div className="flex items-start">
            <ShieldCheckIcon className="h-5 w-5 text-indigo-600 mt-0.5 mr-2 flex-shrink-0" />
            <div className="text-sm text-indigo-800">
              <p className="font-medium">Copyright Protected</p>
              <p className="mt-1">
                This poem is protected by copyright and registered in our immutable ledger system. 
                All rights belong to the author under the {poem.license || 'All Rights Reserved'} license.
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default PoemPage;