import React, { useState, useEffect } from 'react';
import { useParams, Link } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import { BookOpenIcon, CalendarIcon, MapPinIcon, GlobeAltIcon, HeartIcon, EyeIcon } from '@heroicons/react/24/outline';
import { UserIcon } from '@heroicons/react/24/solid';
import toast from 'react-hot-toast';

const AuthorPage = () => {
  const { authorId } = useParams();
  const { token } = useAuth();
  const [author, setAuthor] = useState(null);
  const [poems, setPoems] = useState([]);
  const [loading, setLoading] = useState(true);
  const [following, setFollowing] = useState(false);

  useEffect(() => {
    if (authorId) {
      fetchAuthorData();
    }
  }, [authorId, token]);

  const fetchAuthorData = async () => {
    try {
      const API_BASE_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000/api';
      
      // Fetch author details
      const authorResponse = await fetch(`${API_BASE_URL}/users/${authorId}`, {
        headers: token ? { 'Authorization': `Bearer ${token}` } : {}
      });
      
      if (!authorResponse.ok) {
        throw new Error('Author not found');
      }
      
      const authorData = await authorResponse.json();
      setAuthor(authorData.user);
      
      // Fetch author's public poems
      const poemsResponse = await fetch(`${API_BASE_URL}/poems/user/${authorId}/public`, {
        headers: token ? { 'Authorization': `Bearer ${token}` } : {}
      });
      
      if (poemsResponse.ok) {
        const poemsData = await poemsResponse.json();
        setPoems(poemsData.poems || []);
      }
      
    } catch (error) {
      console.error('Error fetching author:', error);
      toast.error('Failed to load author profile');
    } finally {
      setLoading(false);
    }
  };

  const handleFollowToggle = async () => {
    if (!token) {
      toast.error('Please log in to follow authors');
      return;
    }

    try {
      const API_BASE_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000/api';
      const action = following ? 'unfollow' : 'follow';
      
      const response = await fetch(`${API_BASE_URL}/users/${authorId}/${action}`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });

      if (!response.ok) {
        throw new Error(`Failed to ${action} author`);
      }

      setFollowing(!following);
      toast.success(following ? 'Unfollowed successfully' : 'Following successfully');
    } catch (error) {
      console.error('Error toggling follow:', error);
      toast.error('Failed to update follow status');
    }
  };

  const formatDate = (dateString) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'long'
    });
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-slate-50 to-blue-50 py-8">
        <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="animate-pulse">
            <div className="bg-white rounded-lg shadow-sm p-8 mb-8">
              <div className="flex items-center space-x-6">
                <div className="h-24 w-24 bg-gray-200 rounded-full"></div>
                <div className="flex-1">
                  <div className="h-8 bg-gray-200 rounded w-1/3 mb-2"></div>
                  <div className="h-4 bg-gray-200 rounded w-1/2 mb-4"></div>
                  <div className="h-4 bg-gray-200 rounded w-2/3"></div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }

  if (!author) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-slate-50 to-blue-50 py-8">
        <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center py-12">
            <UserIcon className="mx-auto h-12 w-12 text-gray-400" />
            <h3 className="mt-2 text-lg font-medium text-gray-900">Author Not Found</h3>
            <p className="mt-1 text-sm text-gray-500">This author profile doesn't exist or has been removed.</p>
            <div className="mt-6">
              <Link
                to="/explore"
                className="inline-flex items-center px-4 py-2 border border-transparent shadow-sm text-sm font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700"
              >
                Explore Other Authors
              </Link>
            </div>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-blue-50 py-8">
      <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Author Profile Header */}
        <div className="bg-white rounded-lg shadow-sm p-8 mb-8">
          <div className="flex items-start space-x-6">
            <div className="flex-shrink-0">
              {author.avatar ? (
                <img 
                  src={author.avatar} 
                  alt={author.displayName || author.username}
                  className="h-24 w-24 rounded-full object-cover"
                />
              ) : (
                <div className="h-24 w-24 rounded-full bg-indigo-100 flex items-center justify-center">
                  <UserIcon className="h-12 w-12 text-indigo-600" />
                </div>
              )}
            </div>
            
            <div className="flex-1 min-w-0">
              <div className="flex items-center justify-between">
                <div>
                  <h1 className="text-2xl font-bold text-gray-900">
                    {author.displayName || author.username}
                  </h1>
                  <p className="text-sm text-gray-500">@{author.username}</p>
                </div>
                
                {token && (
                  <button
                    onClick={handleFollowToggle}
                    className={`px-4 py-2 rounded-md text-sm font-medium focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 ${
                      following
                        ? 'bg-gray-200 text-gray-800 hover:bg-gray-300'
                        : 'bg-indigo-600 text-white hover:bg-indigo-700'
                    }`}
                  >
                    {following ? 'Following' : 'Follow'}
                  </button>
                )}
              </div>
              
              {author.profile?.bio && (
                <p className="mt-3 text-gray-700">{author.profile.bio}</p>
              )}
              
              <div className="mt-4 flex items-center space-x-4 text-sm text-gray-500">
                <div className="flex items-center">
                  <CalendarIcon className="h-4 w-4 mr-1" />
                  Joined {formatDate(author.profile?.joinedAt || author.createdAt)}
                </div>
                
                {author.profile?.location && (
                  <div className="flex items-center">
                    <MapPinIcon className="h-4 w-4 mr-1" />
                    {author.profile.location}
                  </div>
                )}
                
                {author.profile?.website && (
                  <div className="flex items-center">
                    <GlobeAltIcon className="h-4 w-4 mr-1" />
                    <a 
                      href={author.profile.website} 
                      target="_blank" 
                      rel="noopener noreferrer"
                      className="text-indigo-600 hover:text-indigo-500"
                    >
                      Website
                    </a>
                  </div>
                )}
              </div>
              
              <div className="mt-4 flex items-center space-x-6 text-sm">
                <span className="font-medium text-gray-900">
                  {poems.length} {poems.length === 1 ? 'Poem' : 'Poems'}
                </span>
                <span className="font-medium text-gray-900">
                  {author.followers || 0} {author.followers === 1 ? 'Follower' : 'Followers'}
                </span>
                <span className="font-medium text-gray-900">
                  {author.following || 0} Following
                </span>
              </div>
            </div>
          </div>
        </div>

        {/* Author's Poems */}
        <div className="bg-white rounded-lg shadow-sm">
          <div className="px-6 py-4 border-b border-gray-200">
            <h2 className="text-lg font-medium text-gray-900">Published Poems</h2>
            <p className="text-sm text-gray-500">Public works by {author.displayName || author.username}</p>
          </div>

          {poems.length === 0 ? (
            <div className="text-center py-12">
              <BookOpenIcon className="mx-auto h-12 w-12 text-gray-400" />
              <h3 className="mt-2 text-sm font-medium text-gray-900">No public poems yet</h3>
              <p className="mt-1 text-sm text-gray-500">
                {author.displayName || author.username} hasn't published any public poems yet.
              </p>
            </div>
          ) : (
            <div className="divide-y divide-gray-200">
              {poems.map((poem) => (
                <div key={poem.id || poem._id} className="p-6 hover:bg-gray-50">
                  <div className="flex items-start justify-between">
                    <div className="flex-1 min-w-0">
                      <Link
                        to={`/poems/${poem.id || poem._id}`}
                        className="text-lg font-medium text-gray-900 hover:text-indigo-600 block"
                      >
                        {poem.title}
                      </Link>
                      
                      <div className="mt-2 text-gray-600 text-sm line-clamp-3">
                        {poem.body.length > 150 
                          ? `${poem.body.substring(0, 150)}...` 
                          : poem.body
                        }
                      </div>
                      
                      <div className="mt-3 flex items-center space-x-4 text-xs text-gray-500">
                        <span>{formatDate(poem.createdAt)}</span>
                        <div className="flex items-center">
                          <EyeIcon className="h-3 w-3 mr-1" />
                          {poem.views || 0}
                        </div>
                        <div className="flex items-center">
                          <HeartIcon className="h-3 w-3 mr-1" />
                          {poem.likes?.length || 0}
                        </div>
                        {poem.tags && poem.tags.length > 0 && (
                          <span className="inline-flex items-center px-2 py-0.5 rounded text-xs font-medium bg-indigo-100 text-indigo-800">
                            {poem.tags[0]}
                          </span>
                        )}
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default AuthorPage;